import {
  getArticles,
  favorArcticles,
  getTags,
  faviorArticle,
  removeFavior,
} from "@/api/article.js";
export default {
  name: "home",
  async asyncData({ query, store }) {
    const limit = 2;
    const page = Number.parseInt(query.page || 1);
    const tab = query.tab || "global_feed";

    const loadArticle =
      store.state.user && tab === "your_feed" ? favorArcticles : getArticles;
    const [articlesRes, tagRes] = await Promise.all([
      loadArticle({
        limit: limit,
        offset: (page - 1) * limit,
        tag: query.tag,
      }),
      getTags(),
    ]);
    const { articles, articlesCount } = articlesRes.data;
    articles.forEach((article) => (article.disabled = false));
    return {
      articles,
      articlesCount,
      limit,
      page,
      tag: "tag",
      tab,
      tagList: tagRes.data.tags,
    };
  },
  watchQuery: ["page", "tag", "tab"],
  computed: {
    pages() {
      return Math.ceil(this.articlesCount / this.limit);
    },
  },
  methods: {
    async toggleFeed(item) {
      console.log(item);
      item.disabled = true;
      if (item.favorited) {
        await removeFavior(item.slug);
        item.favorited = false;
        item.favoritesCount -= 1;
      } else {
        await faviorArticle(item.slug);
        item.favorited = true;
        item.favoritesCount += 1;
      }

      item.disabled = false;
    },
  },
};
